.catalog {
  background-color: $color-whisper;
  padding: 108px 8.6%;

  @media screen and (max-width: 1023px) {
    padding: 100px 60px;
}
@media screen and (max-width: 1023px) {
  padding: 65px 7.8%;
}
}

.catalog__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: 1024px) {
  max-width: 1130px;
}

@media screen and (max-width: 1023px) {
  width: 648px;
}

@media screen and (max-width: 767px) {
  width: calc(270px + (100vw - 320px) * (270 - 646) / (320 - 767));
  max-width: 646px;
}
}

.catalog__title {
  font-family: "Montserrat Semibold", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  margin: 0;
  margin-bottom: 60px;
  text-align: center;
  color: $color-tangaroa;

  @media screen and (max-width: 1023px) {
    font-size: 26px;
    line-height: 39px;
}


@media screen and (max-width: 767px) {
  font-size: 24px;
  line-height: 36px;
}
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(4, minmax(200px, 260px));
  grid-column-gap: 2.7%;
 // grid-column-gap: calc(10px + (100vw - 1024px) * (10 - 30) / (1024 - 1366));
  grid-row-gap: 30px;
  grid-template-rows: auto;
  list-style-type: none;
  margin: 0;
  padding: 0;

  @media (min-width: 1024px) {
  max-width: 1130px;
}

@media screen and (max-width: 1300px) {
  grid-template-columns: repeat(3,260px);
  grid-column-gap: calc(17px + (100vw - 1024px) * (17 - 70) / (1024 - 1300));
  padding-right: calc(17px + (100vw - 1024px) * (17 - 78) / (1024 - 1300));
  padding-left:  calc(17px + (100vw - 1024px) * (17 - 78) / (1024 - 1300));
}

@media screen and (max-width: 1023px) {
  grid-template-columns: repeat(2, 309px);
  width: 648px;
  grid-column-gap: 30px;
}

@media screen and (max-width: 767px) {
  grid-template-columns: repeat(2, minmax(245px, 300px));
  grid-column-gap: calc(15px + (100vw - 600px) * (15 - 70) / (600 - 767));
  width: calc(506px + (100vw - 600px) * (506 - 645) / (600 - 767));
}

@media screen and (max-width: 600px) {
grid-template-columns: minmax(270px, 646px);
width: calc(270px + (100vw - 320px) * (270 - 646) / (320 - 767));
}

@media screen and (max-width: 319px) {
  grid-template-columns: minmax(230px, 270px);
  width: calc(230px + (100vw - 270px) * (230 - 270) / (270 - 319));
}
}

.catalog__item {
  padding: 35px 9.6%;
  min-height: 386px;
  background-color: $color-tangaroa;
  cursor: pointer;
  box-sizing: border-box;
  margin: 0;
  color: $color-alice-blue;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;

  @media screen and (max-width: 1023px) {
    padding: 35px;
}
@media screen and (max-width: 767px) {
padding: 35px 9.6%;
}
}

.catalog__item:hover,
.catalog__item:focus {
  background-image: none;
}

.catalog__text-wrapper{
display: none;
}

.catalog__item:hover .catalog__text-wrapper,
.catalog__item:focus .catalog__text-wrapper {
 display: block;
}

