.catalog__item-title {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  margin: 0;
}

.catalog__item-title--up {
margin-bottom: 25px;
}

.catalog__item:hover .catalog__item-title--down,
.catalog__item:focus .catalog__item-title--down {
  display: none;
}

.catalog__item-title--down {
  @media screen and (max-width: 600px) and (min-width: 510px) {
color: $color-tangaroa;
font-family: "Montserrat Bold", "Arial", sans-serif;
font-weight: 700;
}
}

.catalog__item-text {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
  margin: 0;
  margin-bottom: 20px;
}

.catalog__item-text:last-of-type {
  margin-bottom: 30px;
}

.catalog__item-text--subtitle {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-weight: 500;
  margin-bottom: 10px;
}

.catalog__button {
background-color: $color-alice-blue;
color: $color-tangaroa;
font-family: "Montserrat Semibold", "Arial", sans-serif;
font-size: 14px ;
font-weight: 600;
line-height: 21px;
padding: 14px 18%;
text-align: center;
margin: 0;
display: block;
width: calc(100px + (210 - 100) * ((100vw - 500px) / (1366 - 500)));
max-width: 210px;

@media screen and (max-width: 1300px) {
  width: 210px;
}

@media screen and (max-width: 1023px) {
  width:239px;
  }

@media screen and (max-width: 767px) {
width: calc(190px + (100vw - 600px) * (190 - 240) / (600 - 767));
padding: 14px 23.6%;
}

@media screen and (max-width: 600px) {
width: calc(210px + (100vw - 320px) * (210 - 408) / (320 - 600));
max-width: 408px;
}

@media screen and (max-width: 319px) {
  font-size: 12px ;
line-height: 19px;
}
}
.catalog__button:hover,
.catalog__button:focus {
background-color: $color-pale-cornflower-blue;
}
